<template>
  <transition name='faded'>
    <div class="modal-container" :class="{'modal-warning':type==='warning'}">
      <div @click="clickShadow" class="modal-shadow"></div>
      <div class="modal" v-loading="loading">
        <div class="modal-header">
          <h5 class="modal-title">{{modalTitle}}</h5>
          <span v-if="!hiddenCancelIcon" @click="clickClose" class="modal-close-icon"></span>
        </div>
        <div class="modal-body">
          <div class="modal-content">
            <slot></slot>
          </div>
          <div class="modal-footer">
            <button v-if="!hiddenCancelBtn" @click="cancel" class='cancel-button'>{{cancelText}}</button>
            <button @click="confirm" class='confirm-button' :class="{'confirm-loading':confirmLoading}">
              <i v-if="confirmLoading" class="el-icon-loading"></i>
              {{confirmText}}
            </button>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    type: String,
    modalTitle: String,
    loading: Boolean,
    cancelText: {
      default: '取消',
      required: false
    },
    confirmText: {
      default: '确认',
      required: false
    },
    hiddenCancelBtn: Boolean,
    hiddenCancelIcon: Boolean,
    clickShadowButNotCancel: {
      default: false
    }
  },
  data () {
    return {
      confirmLoading: false
    }
  },
  methods: {
    clickClose () {
      if (this.clickShadowButNotCancel) {
        this.$emit('clickShadow')
      } else {
        this.$emit('cancelModal')
      }
    },
    clickShadow () {
      if (this.clickShadowButNotCancel) {
        this.$emit('clickShadow')
      } else {
        this.$emit('cancelModal')
      }
    },
    cancel () {
      this.$emit('cancelModal')
    },
    confirm () {
      if (this.confirmLoading) {
        return
      }
      this.$emit('confirmModal', {
        wait: () => {
          this.confirmLoading = true
        },
        finish: () => {
          this.confirmLoading = false
        }
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
  .modal-container
    position fixed
    z-index 20
    top 0
    left 0

  .modal-shadow
    position fixed
    top 0
    left 0
    height 100vh
    width 100vw
    background-color rgba(0, 0, 0, .3)
    z-index 24

  .modal
    position fixed
    top 50%
    left 50%
    transform translate(-50%, -50%)
    display inline-block
    min-width 380px
    border-radius 8px
    overflow hidden
    z-index 25

  .modal-header
    padding 0 20px
    height 40px
    display flex
    justify-content space-between
    align-items center
    color #ffffff
    background-color rgba(36, 207, 95, 1)

  .modal-body
    background white

  .modal-title
    font-size 16px

  .modal-close-icon
    height 12px
    width 12px
    background url('~@/assets/white_wrong.png') no-repeat center
    background-size 12px 12px
    cursor pointer

  .modal-content
    padding 20px 20px 10px
    overflow auto
    font-size 12px

  .modal-footer
    padding 20px 0
    text-align center
    font-size 14px

  .cancel-button, .confirm-button
    display inline-block
    width 110px
    height 35px
    line-height 35px
    text-align center
    border-radius 35px
    border none
    cursor pointer

  .cancel-button
    margin-right 30px
    color #666666
    background-color #eaeaea
    box-shadow 1px 1px 5px #dddddd
    transition background-color .4s
    opacity .9
    &:hover
      opacity 1

  .confirm-button
    color #ffffff
    background-color rgba(36, 207, 95, .9)
    box-shadow 1px 1px 5px rgba(36, 207, 95, 1)
    transition all .4s
    &:hover
      background-color rgba(36, 207, 95, 1)

  .faded-enter-active, .faded-leave-active
    transition opacity .2s ease-in-out

  .faded-enter, .faded-leave-to
    opacity 0

  .modal-warning.modal-container .modal-header
    background-color #F57070

  .modal-warning.modal-container .confirm-button
    color #666666
    background-color #eaeaea
    box-shadow 1px 1px 5px #dddddd

  .confirm-button.confirm-loading
    opacity 0.5
    cursor wait
</style>



