<template>
  <PageBody>
    <Breadcrumb>
      <router-link tag="li" :to="'/user/screen/top'">三屏播放</router-link>
      <li v-if="$route.params.type === 'top'">上屏</li>
      <li v-else-if="$route.params.type === 'middle'">中屏</li>
      <li v-else-if="$route.params.type === 'bottom'">下屏</li>
    </Breadcrumb>
    <div class="greeting-form" v-loading="loading">
      <Form label-position="right" :label-width="124" @submit.native.prevent @keydown.native.enter.prevent>
        <FormItem label="文件名">
          <Input v-model="form.name" :maxlength="119" size="default" style="width:300px" placeholder="请输入文件名"
                 :disabled="isPlaying"/>
        </FormItem>
        <FormItem label="播放时间">
          <Select :transfer="false" size="default" :disabled="isPlaying" :clearable="!isPlaying"
                  v-model="form.time" style="width:300px" placeholder="不限时间">
            <Option value="0">不限时间</Option>
            <Option value="2">设置日期</Option>
          </Select>
          <!--先保留-->
          <div v-show="Number(form.time)===1" style="margin-top: 24px">
            <DatePicker type="datetimerange" format="yyyy-MM-dd HH:mm" size="default" :transfer="false"
                        :editable="false" placeholder="请选择播放时间范围" style="width: 300px"
                        :options="dateOption" :value="form.range"
                        @on-change="(date)=>{form.range=date}"
                        :disabled="isPlaying" :readonly="isPlaying"/>
            <p v-if="isPlaying" style="color: #b1b1b1;">在播文件夹时间不可修改</p>
          </div>
          <template v-if="Number(form.time)===2">
            <div style="margin-top: 24px">
              <TimePicker
                type="timerange"
                style="width: 300px"
                size="default"
                :editable="false"
                :clearable="false"
                :transfer="false"
                format="HH:mm"
                placeholder="选择时间"
                :disabled="isPlaying"
                :readonly="isPlaying"
                :value="time"
                @on-change="handleTime"
                @on-open-change="handleTimeCheck"
              />
            </div>
            <div style="margin-top: 24px;width: 300px;" v-loading="checking">
              <DatePicker
                type="date"
                style="width: 300px"
                size="default"
                :editable="false"
                :transfer="false"
                multiple
                placeholder="选择日期"
                :options="{disabledDate}"
                :disabled="isPlaying"
                :readonly="isPlaying"
                :value="date"
                @on-change="handleDate"
              />
            </div>
            <p v-if="isPlaying" style="color: #b1b1b1;">在播文件夹时间不可修改</p>
          </template>
        </FormItem>
        <FormItem>
          <button class="form-btn" @click="reset">重置</button>
          <button class="form-btn primary-btn" :class="{submitting:submitting}" @click="submit">确定</button>
        </FormItem>
      </Form>
    </div>

    <Modal
      v-if="isConfirming"
      modalTitle="复播提示"
      cancelText="不复播"
      confirmText="复播"
      @cancelModal="handleSubmit"
      @confirmModal="handleConfirm"
    >是否复播该文件夹
    </Modal>
  </PageBody>
</template>
<script>
import Breadcrumb from 'views/components/Breadcrumb'

import { getToken } from 'utils/tool'
import Utils from 'utils/utils'
import Validate from 'utils/validate'
import Modal from 'components/Modal'
import { TimePicker } from 'iview'
import { addNewFolder, getFolderById, updateFolder, checkFolderTime } from 'api/screen'

const NOW_TIME = new Date().getTime()
export default {
  components: {Breadcrumb, Modal, TimePicker},
  data () {
    return {
      isConfirming: false,
      loading: false,
      submitting: false,
      selecting: false,
      selected: [],
      form: {
        name: '',
        time: '',
        range: []
      },
      dateOption: {
        disabledDate (date) {
          const time = date.getTime()
          return time <= (NOW_TIME - 24 * 60 * 60 * 1000)
        }
      },
      time: '',
      date: '',
      checkedDate: [],
      checking: false
    }
  },
  computed: {
    disabledDate: function () {
      let array = this.checkedDate
      let minDateTime = new Date().getTime() - 24 * 60 * 60 * 1000
      let maxDateTime = minDateTime + 90 * 24 * 60 * 60 * 1000
      return (date) => {
        const dateTime = date.getTime()
        if (array.indexOf(Utils.formatStamp(dateTime, 'YYYY-MM-DD')) !== -1) {
          return true
        }
        return dateTime > maxDateTime || dateTime < minDateTime
      }
    },
    isPlaying: function () {
      if (this.$route.params.id) {
        if (this.status) {
          if (Number(this.form.time) === 0) {
            return true
          } else if (this.form.range[0] && this.form.range[1]) {
            if (NOW_TIME > new Date(this.form.range[0]).getTime() && NOW_TIME < new Date(this.form.range[1]).getTime()) {
              return true
            }
          }
        }
      }
    },
    classify: function () {
      return this.$route.query.classify === 'picture' ? '图片' : '视频'
    },
    type: function () {
      switch (this.$route.params.type) {
        case 'top':
          return 0
        case 'middle':
          return 1
        case 'bottom':
          return 2
      }
    },
    selected_input_show: function () {
      let stores = this.stores
      let show = ''
      for (let index in stores) {
        if (stores.hasOwnProperty(index)) {
          let store = stores[index]
          show += store[0].storeName + ': '
          show += store.map(value => value.name).join('、') + '　　'
        }
      }
      return show
    },
    stores: function () {
      let stores = {}
      this.selected.forEach((value) => {
        if (!stores[value.storeId]) {
          stores[value.storeId] = []
        }
        stores[value.storeId].push(value)
      })
      return stores
    }
  },
  watch: {
    'form.name': function (value) {
      if (value.length > 120) {
        this.$message({
          message: '文件夹名称最长120个字符',
          type: 'warning'
        })
      }
    },
    checkedDate: function (newDate) {
      let selected = this.date.split(',')
      let isExist = false
      let roundingDate = new Date(Utils.formatStamp(NOW_TIME, 'YYYY-MM-DD')).getTime()
      selected = selected.filter((v) => new Date(v).getTime() >= roundingDate)
      newDate.forEach((v) => {
        let index = selected.indexOf(v)
        if (index !== -1) {
          isExist = true
          selected.splice(index, 1)
        }
      })
      if (isExist) {
        this.$message({
          message: '播放日期有冲突，请重新确认',
          type: 'warning'
        })
      }
      this.date = selected.join(',')
    }
  },
  mounted () {
    this.token = getToken()
    this.form.user_id = this.token.id
    if (this.$route.params.id) {
      this.loading = true
      let {id} = this.$route.params
      let type = this.type
      let userId = this.token.id
      getFolderById({id, user_id: userId, type}).then(res => {
        if (Number(res.code) === 200) {
          this.status = Number(res.data.status)
          this.form = {
            name: res.data.folderName,
            time: String(res.data.unlimitedTime),
            range: [
              res.data.startTime ? res.data.startTime.substr(0, res.data.startTime.length - 3) : '',
              res.data.endTime ? res.data.endTime.substr(0, res.data.startTime.length - 3) : ''
            ]
          }
          this.time = res.data.rePlayTime.split('-').map(v => v.slice(0, -3))
          this.date = res.data.rePlayDate
          this.isReplay = this.$route.query.type === 'replay'
          this.$router.replace({
            path: this.$route.path,
            query: {
              classify: Number(res.data.contentType) === 0 ? 'picture' : 'video'
            }
          })
          this.checkTime2('time').catch((err) => {
            this.$message({
              message: err.message,
              type: 'warning'
            })
          })
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
        this.loading = false
      })
    }
  },
  methods: {
    handleTime (t) {
      this.time = t
    },
    handleDate (d) {
      this.date = d
    },
    handleTimeCheck (status) {
      if (!status) {
        this.$nextTick(function () {
          let start = new Date('2018-11-11 ' + this.time[0]).getTime()
          let end = new Date('2018-11-11 ' + this.time[1]).getTime()
          if ((end - start) < 2 * 60 * 60 * 1000) {
            this.$message({
              message: '开始时间与结束时间至少间隔2小时',
              type: 'warning'
            })
            this.handleTime('')
            return
          }
          this.checkTime2('time').catch((err) => {
            this.$message({
              message: err.message,
              type: 'warning'
            })
            this.handleTime('')
          })
        })
      }
    },
    checkTime2 (type) {
      return new Promise((resolve, reject) => {
        let data = {
          screenType: this.type,
          unlimited_time: this.form.time,
          rePlayTime: this.time[0] + ':00-' + this.time[1] + ':00'
        }
        if (this.$route.params.id) {
          data['id'] = this.$route.params.id
        }
        if (data['rePlayTime']) {
          this.checking = true
          checkFolderTime(data).then(res => {
            this.checking = false
            if (Number(res.code) === 200) {
              this.checkedDate = res.data
              let nowDate = Utils.formatStamp(NOW_TIME, 'YYYY-MM-DD')
              let start = new Date(nowDate + ' ' + this.time[0]).getTime()
              if ((NOW_TIME + 2 * 60 * 60 * 1000) > start) {
                this.$message({
                  message: '播放的开始时间必须为当前时间的2小时后以后',
                  type: 'warning'
                })
                this.checkedDate.push(nowDate)
              }
              resolve()
            } else {
              reject(new Error(res.message))
            }
          })
        } else {
          resolve()
        }
      })
    },
    handleSelect () {
      if (!this.isPlaying) {
        this.selecting = true
      }
    },
    // checkTime () {
    //   let start = new Date(this.form.range[0]).getTime()
    //   if ((NOW_TIME + 4 * 60 * 60 * 1000) > start) {
    //     this.form.range = []
    //     this.$message({
    //       message: '播放的开始时间必须为当前时间的4小时后以后',
    //       type: 'warning'
    //     })
    //     return false
    //   }
    //   let end = new Date(this.form.range[1]).getTime()
    //   if ((end - start) < 2 * 60 * 60 * 1000) {
    //     this.form.range = []
    //     this.$message({
    //       message: '开始时间与结束时间至少间隔2小时',
    //       type: 'warning'
    //     })
    //     return false
    //   }
    //   return true
    // },
    terminalConfirm (selected) {
      this.selected = selected
      this.selecting = false
    },
    validate () {
      if (Validate.isNullString(this.form.name)) {
        this.$message({
          message: '请输入文件名',
          type: 'warning'
        })
        return false
      } else if (Number(this.form.time) === 1) {
        if (!this.form.range[0] || !this.form.range[1]) {
          this.$message({
            message: '请选择播放时间',
            type: 'warning'
          })
          return false
        }
      } else if (Number(this.form.time) === 2) {
        if (!this.time) {
          this.$message({
            message: '请选择播放时间范围',
            type: 'warning'
          })
          return false
        } else if (!this.date) {
          this.$message({
            message: '请选择播放日期',
            type: 'warning'
          })
          return false
        }
      }
      return true
    },
    reset () {
      this.form.name = ''
      if (!this.isPlaying) {
        this.selected = []
        this.form.range = ''
        this.form.time = ''
      }
    },
    handleConfirm () {
      this.isReplay = true
      this.handleSubmit()
    },
    submit () {
      if (!this.submitting && this.validate()) {
        if (this.$route.params.id && !this.isReplay) {
          this.isConfirming = true
        } else {
          this.handleSubmit()
        }
      }
    },
    handleSubmit () {
      this.isConfirming = false
      this.submitting = true
      let data = {
        replay: this.isReplay ? 1 : 0,
        userId: this.token.id,
        type: this.type,
        content_type: this.$route.query.classify === 'picture' ? 0 : 1,
        folderName: this.form.name,
        roleId: Object.keys(this.stores).join(','),
        machine_ids: this.selected.map(value => value.id).join(','),
        unlimited_time: this.form.time === '' ? '0' : this.form.time,
        startTime: this.form.range[0] ? this.form.range[0] + ':00' : '',
        endTime: this.form.range[1] ? this.form.range[1] + ':00' : '',
        rePlayTime: this.time[0] + ':00-' + this.time[1] + ':00',
        rePlayDate: this.date.split(',').map(v => new Date(v).getTime()).sort()
          .map((v) => Utils.formatStamp(v, 'YYYY-MM-DD')).join(',')
      }
      if (Number(data['unlimited_time']) === 2) {
        let dataArray = data['rePlayDate'].split(',')
        data['startTime'] = dataArray[0] + ' ' + this.time[0] + ':00'
        data['endTime'] = dataArray[dataArray.length - 1] + ' ' + this.time[1] + ':00'
      }
      let submit = addNewFolder
      if (this.$route.params.id) {
        submit = updateFolder
        data['id'] = this.$route.params.id
      }
      submit(data).then(res => {
        this.submitting = false
        if (Number(res.code) === 200) {
          this.$message({
            message: '操作成功',
            type: 'success'
          })
          if (!this.$route.params.id) {
            this.$router.push({path: '/screen/folder/' + res.data})
          } else {
            this.$router.back()
          }
        } else {
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      })
    }
  }
}
</script>
<style lang='stylus' scoped>
  .greeting-form
    width 664px
    font-size 14px
  .greeting-form /deep/ .ivu-form-item-label
    font-size 14px
  .greeting-form /deep/ .ivu-input
    font-size 14px
  .edit-store
    position: absolute
    top 1px
    bottom 1px
    right 0
    width 35px
    border-left 1px solid #EAEAEA
    display flex
    align-items center
    justify-content center
    cursor pointer
  .edit-store-icon
    display inline-block
    background url('~@/assets/edit.png') no-repeat center
    background-size 100% 100%
    width 14px
    height 14px
  .form-btn
    margin-right 40px
    background #EAEAEA
    color #666666
    width 110px
    height 35px
    line-height 35px
    box-shadow 0px 2px 4px 0px rgba(234, 234, 234, 0.6)
    border-radius 20px
    border none
    font-size 14px
    outline none
    &.primary-btn
      background #24CF5F
      color #FFFFFF
      box-shadow 0px 2px 4px 0px rgba(36, 207, 95, 0.6)
  .submitting
    opacity 0.5
    cursor progress
  .select-input
    position relative
  .select-input /deep/ .ivu-input
    padding-right 35px
    text-overflow: ellipsis;
</style>



